@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;1,900&family=Ubuntu:wght@300;400;500&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  font-family: 'Ubuntu', sans-serif;
  font-size: 18px;
}

body{
  display: flex;
  justify-content: center;
  align-items: center;
}

.container{
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  margin: 0 auto;
  width: 275px;
  text-align: center;
  border: 10px solid #101116;
  background: #101116;
  border-radius: 5px;
}

.keypad{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(60px, auto);
}

input[type = "text"] {
  height: 70px;
  margin: 10px 0;
  width: 251px;
  background-color: rgb(23, 126, 3);
  border: 0px;
  outline: none;
  color: #000;
  text-align: right;
  padding-top: 50px;
  font-size: 25px;
  font-weight: 500;
  letter-spacing: 1px;
}

#clear{
  grid-column: 1/3;
  grid-row: 1;
  color: #ffffff;
  border-radius: 5%;
}

#result{
  grid-column: 3/5;
  grid-row: 5;
  color: #ffffff;
  border-radius: 5%;
}

#backspace{
  color: #ffffff;
}

button{
  margin: 5px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  background: #262834;
  color: #ffffff;
  font-weight: 500;
}

.highlight{
  background: #c5a30b;
}

video{
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  position: absolute;
}